import React, { useEffect, useState } from "react";
import { useParams, Link } from "react-router-dom";
import axios from "axios";
import VideoSection from "../components/VideoSection";

const MovieDatail = () => {
  const { id } = useParams();
  const [movieDetail, setMovieDetail] = useState("");
  const [video, setVideo] = useState();

  const API_KEY = process.env.REACT_APP_TMDB_KEY;
  const MOVIE_DETAIL = `https://api.themoviedb.org/3/movie/${id}?api_key=${API_KEY}`;
  const VIDEO_URL = `https://api.themoviedb.org/3/movie/${id}/videos?api_key=${API_KEY}`;
  const baseImageUrl = "https://image.tmdb.org/t/p/w1280";
  const defaultImage =
    "https://images.unsplash.com/photo-1581905764498-f1b60bae941a?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=700&q=80";

  useEffect(() => {
    axios
      .get(MOVIE_DETAIL)
      .then((response) => setMovieDetail(response.data))
      .catch((err) => console.log(err));
    axios
      .get(VIDEO_URL)
      .then((res) => setVideo(res.data.results[0].key))
      .catch((err) => console.log(err));
  }, [MOVIE_DETAIL, VIDEO_URL]);

  const {
    poster_path,
    title,
    overview,
    vote_average,
    release_date,
    vote_count,
  } = movieDetail;

  return (
    <div className="container py-5">
      <h1 className="text-center">{title}</h1>
      {video && <VideoSection video={video} />}
      <div className="card mb-3">
        <div className="row g-0">
          <div className="col-md-4">
            <img
              src={poster_path ? baseImageUrl + poster_path : defaultImage}
              className="img-fluid rounded-start"
              alt="..."
            />
          </div>
          <div className="col-md-8 d-flex flex-column ">
            <div className="card-body">
              <h5 className="card-title">Overview</h5>
              <p className="card-text">{overview}</p>
            </div>
            <ul className="list-group ">
              <li className="list-group-item">
                {"Release Date : " + release_date}
              </li>
              <li className="list-group-item">{"Rate : " + vote_average}</li>
              <li className="list-group-item">
                {"Total Vote : " + vote_count}
              </li>
              <li className="list-group-item">
                <Link to={-1} className="card-link">
                  Go Back
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
};

export default MovieDatail;
